.example-img {
  padding: 40px 0;
}

.img-property {
  width: 100%;
}

.img-property-small {
  display: none;
}

@media (min-width: 1200px) {
  .img-property {
    width: 85%;
  }
}

@media (max-width: 769px) {
  .img-property-big {
    display: none;
  }
  .img-property-small {
    display: block;
    width: 95%;
  }
}



