 .header {
  background: var(--main-color);
  color: var(--white);
  display: -ms-flexbox;
  display: flex;
  height: 80px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 6px 10px 6px 6px;
  grid-column: header;

}
.logo {
  display: -ms-flexbox;
  display: flex;
}

.logo a {
  color: var(--white);
  text-decoration: none;
  margin: 0;
}

.logo h1 {
  background: var(--second-color);
  font-family: 'Roboto Slab', serif;
  font-size: 1.8rem;
  padding: 20px;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.header p {
  margin: 0;
}

.button-searcher {
  display: none;
}
/* social icons */
.share-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.share-links-icon {
  margin-left: 10px;
}

.share-links-icon svg {
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}
.share-links-icon:hover svg {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);

}
/* breakpoints */
@media (max-width: 769px) {
  .header {
    height: 50px;
  }

  .button-searcher {
    display: -ms-flexbox;
    display: flex;
    margin-left: 8px;
  }

  .logo h1 {
    font-size: 1.3rem;
    padding: 10px 12px;
    margin-left: 14px;
  }

  .share-links p {
    display: none;
  }
  .share-links-icon {
    margin-left: 8px;
  }
}

@media (max-width: 330px) {
  .button-searcher {
    display: -ms-flexbox;
    display: flex;
    margin-left: 6px;
  }
  .logo h1 {
    font-size: 1.4rem;
    padding: 12px 10px;
    margin-left: 10px;
  }
}



